import React, { useContext } from "react";
import ElementHeader from "./components/ElementHeader/ElementHeader";
import TextHeader from "./components/TextHeader/TextHeader";
import { DesignToolContext } from "../../../../../../../../contexts/DesignTool/DesignToolContext";
import { designToolReducer } from "../../../../../../../../contexts/DesignTool/DesingToolReducers";

const CardHeader: React.FC = (): JSX.Element => {
    const {
        designToolState
    } = useContext(DesignToolContext)
    return (

        <div style={{ height: "61px" }} className="z-50 flex-col py-2  
        ">
            {designToolState.card_header_state === "text" && <TextHeader />}
            {designToolState.card_header_state === "shape" && <ElementHeader />}
        </div>
    );
};
export default CardHeader;
